@import "common";
.page_about {
  .container {
    flex-direction: column;
    justify-content: center; }

  * {
 }    // border: solid 1px
  section {
    min-height: 0px;
    .section_title {
      padding-top: $gutter_width*0;
      margin-top: 0;
      margin-bottom: $gutter_width*2;
      font-weight: 300;
      font-size: 44px;
      opacity: 0.8;
      letter-spacing: 10px; } }

  nav.navbar-default.at_top {
    background-color: #FFF; }

  .section_hero {
    min-height: 140vh;
    padding-top: $nav_height;
    display: flex;
    background: url("../img/homepage/About1.jpg");
    // background-attachment: fixed
    background-size: auto 120%;
    background-position: center bottom;
    padding-bottom: 30vh;


    &,h1 {
      color: $color_white;
      opacity: 0.95; }
    h1 {
      margin-bottom: $gutter_width*2; }
    p {
      color: $color_white;
      font-weight: 300;
      opacity: 0.9; } }


  .section_title {
    padding-top: $gutter_width*4; }

  .section_log {
    padding-top: $gutter_width*8;
    .container {
      flex-direction: column; }
    .logs_area {
      background-color: $color_white;
      padding: grid_sz(0,1);
      width: 100%; }

    .section_title {
      font-weight: 300;
 }      // color: black
    .container {
      margin-top: -45vh; }


    .log_box {
      margin-bottom: $gutter_width;
      display: flex;
      * {
        color: $color_grey_light; }

      .title {
        color: $color_grey_dark;
        margin-bottom: grid_sz(0,1); } }


    .col_cover {
      width: percent_grid(4,3); }
    .col_info {
      width: percent_grid(8,7);
      padding: grid_sz(0,1);
      padding-left: 0px;
      margin-left: grid_sz(0,1);
      border-bottom: solid 1px $color_theme; } }

  .cover_image {
    min-height: 200px;
    background-size: cover;

    &[data-log="1"] {
      background: url("../img/homepage/Post2.jpg");
      background-size: cover; }
    &[data-log="2"] {
      background: url("../img/homepage/Post3.jpg");
      background-size: cover; }
    &[data-log="3"] {
      background: url("../img/homepage/Post4.jpg");
      background-size: cover; } }



  .cover_image[data-people] {
    position: absolute;
    height: 100%;
    width: 30%;
    left: 0;
    top: 0;

    &[data-people="1"] {
      background: url("../img/homepage/About3.jpg");
      background-size: auto 100%; }
    &[data-people="2"] {
      background: url("../img/homepage/About4.jpg");
      background-size: auto 100%; } }

  .person_box {
    padding: $gutter_width*2 $gutter_width*1;
    position: relative;
    display: flex;
    .row {
      position: relative;
      display: flex; }
    font-weight: 800;
    color: rgba(black,0.6);
    h4 {
      font-weight: bold; }
    .bigger_text {
      font-size: 1.1em; }
    ul {
      list-style: none;
      padding: 0;
      li {
        padding-top: 3px;
        padding-bottom: 3px;
        &:before {
          content: ".";
          position: relative;
          display: inline-block;
          top: -5px;
          margin-right: 15px; } } } } }
