@import "common";
.page_news {
  * {
 }    // border: solid 1px

  section {
    min-height: 0px;
    position: relative;
    .section_title {
      // padding-top: $gutter_width*4

      font-weight: 300;
      font-size: 44px;
      opacity: 0.8; } }

  // nav.navbar-default.at_top
  //   background-color: #FFF

  .section_hero {
    height: grid_sz(8,5);
    padding-top: grid_sz(0,9);
    padding-bottom: $column_width*2;

    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    background: url("../img/homepage/News1.jpg");
    background-size: cover;
    background-position: center center;

    .container.flex {
      padding: 0 grid_sz(1,2) grid_sz(0,6) grid_sz(1,2);
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start; }

    &:before {
      @include cover_opacity(rgba(black,0.5)); }
    &,.date,h1 {
      color: white;
      margin: 0;
      font-weight: 300; }
    .date {
      font-size: 16px;
      margin-bottom: -$gutter_width; }
    .tag {
      font-weight: normal;
      height: 30px;
      margin-bottom: 12px;
      padding-left: 12px;
      padding-right: 12px;
      display: inline-block;
      font-size: 14px;
      background-color: rgba($color_white,0.8); }
    h1 {
      font-weight: 300;
      margin-top: $gutter_width*2;
      margin-bottom: $gutter_width*1;
      font-size: 48px;
      letter-spacing: 5px; }

    p {
      color: $color_white; }
    .description {
      max-width: 50%; } }


  .section_news {
    align-items: top;
    .container {
      background-color: $color_white;
      margin-top: -$gutter_width*3;
      width: grid_sz(12,15);
      padding: grid_sz(0,2);
      flex-wrap: wrap;
      // margin-left: -$gutter_width
      // margin-right: $gutter_width
 }      // padding-top: $gutter_width*2



    .news_box {
      display: inline-block;
      margin: $gutter_width/2;
      width: percent_grid(4,3);
      transition: 0s;
      border: solid 1px #eee;
      min-height: 450px;
      transition: 0s;

      &.size_2 {
        width: percent_grid(8,7); }


      a {
        text-decoration: none;
        cursor: pointer;
        &:hover {
          text-decoration: none; } }

      &:before {
        @include cover_opacity(rgba($color_theme,0));
        transition: 0.5s;
        z-index: 10; }

      .btn {
        opacity: 0;
        transition: 0.5s;
        z-index: 20; }

      &:hover {
        &:before {
          @include cover_opacity(rgba($color_theme,0.8)); }
        .btn {
          opacity: 1; } }

      .info {
        padding: $gutter_width*0.8;
        color: $color_grey_light;

        h5.date {
          margin: 0;
          font-size: 16px;
          color: $color_grey_light; }

        h3.title {
          margin-top: 0;
          margin-bottom: $gutter_width/3;
          color: $color_theme;
          font-weight: normal; } } }

    .nav_line_split {
      margin-bottom: grid_sz(0,3);
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      width: 100%;

      &.page_nav {
        li {
          padding-left: $gutter_width/2;
          padding-right: $gutter_width/2;
          margin-bottom: 0px;
          margin-top: grid_sz(0,1); } } }



    .cover {
      min-height: $gutter_width*8;
      @include bg_theme;
      &[data-img="1"] {
        background-image: url("../img/homepage/News1.jpg"); }
      &[data-img="2"] {
        background-image: url("../img/homepage/News2.jpg"); }
      &[data-img="3"] {
        background-image: url("../img/homepage/News3.jpg"); }
      &[data-img="4"] {
        background-image: url("../img/homepage/Post1.jpg"); }
      &[data-img="5"] {
        background-image: url("../img/homepage/Post2.jpg"); }
      &[data-img="6"] {
        background-image: url("../img/homepage/Post3.jpg"); }
      &[data-img="7"] {
        background-image: url("../img/homepage/Post4.jpg"); } } } }

