@import "common";
.page_index {
  opacity: 1;
  .container.flex {
    height: 100vh; }
  section {
    .section_title {
      margin-bottom: $gutter_width*2;
      font-size: 24px;
      letter-spacing: 3px; } }

  .percent {
    font-family: 'Lato';
    font-weight: 300;
    opacity: 0.5;
    font-size: 200px;
    color: $color_theme;
    letter-spacing: 14px;
    vertical-align: middle;
    // margin-bottom: 3*$gutter_width
    transform: skewX(-10deg);
    position: absolute;
    bottom: grid_sz(0,5);
    right: grid_sz(0,3);
    vertical-align: middle;
    @include trans;
    &.initial {
      opacity: 0; }
    &:after {
      content: "%";
      display: inline-block;
      font-weight: 500;
      font-size: grid_sz(0,2.2);
      vertical-align: middle;
      margin-top: -25px;
 } }      // padding-bottom: 50px

  //-----------------------------------------------
  .page_index_main {
    background-image: url("../img/homepage/Home1.jpg");
    min-height: 100vh;
    @include flex_center;
    .row {
      position: relative; }

    h1 {
      font-size: 48px;
      letter-spacing: 15px;
      margin-bottom: 0px;
      margin-top: 0px;
      animation: lspace_in 2s 1 both;
      text-align: center;
      font-weight: 300; }

    @keyframes lspace_in {
      0% {
        letter-spacing: 5px;
        opacity: 0; }
      100% {
        letter-spacing: 15px;
        opacity: 1; } }

    h3.page_header_eng {
      margin-top: 10px;
      font-size: 26px;
      letter-spacing: 2px;
      animation: lspace_in2 2s 0.5s 1 both;
      text-align: center;
      font-weight: 300; }

    @keyframes lspace_in2 {
      0% {
        letter-spacing: 0px;
        opacity: 0; }
      100% {
        letter-spacing: 2px;
        opacity: 1; } }


    .news_container {
      position: absolute;
      bottom: 0px;
      width: grid_sz(10,9);
      height: grid_sz(0,11);

      .timeline {
        width: 100%;
        height: 4px;
        background-color: #ddd;
        position: absolute;
        bottom: 0px;
        z-index: 90;
        .value {
          width: 50%;
          height: 100%;
          background-color: #777;
 } } }          // transition: 0.5s
    .news {
      display: flex;
      flex-direction: row;
      background-color: $color_theme;
      height: 100%;

      opacity: 1;
      @include box_shadow;

      .col_infos,.col_img {
        height: 100%;
        position: relative; }

      .col_infos {
        width: grid_sz(6,6);
        padding: grid_sz(0,1);
        display: flex;
        flex-direction: column;
        justify-content: space-between; }

      .col_img {
        width: grid_sz(4,3); }

      .col_infos .btns {
        @include flex_split("lr");
        width: 100%;
        z-index: 10;

 }        // height: 200px
      h4,h5,p {
        color: $color_white;
        margin: 0;
        font-weight: 300; }
      h5 {
        margin-bottom: $gutter_width; }
      .cover_img {
        background-image: url("../img/homepage/Home2.jpg");
        background-position: center center;
        background-size: auto 110%;
        height: 200px+$gutter_width*3;
        margin-right: 0px;
 } } }        // transition:  background-image 0.5s

  //-----------------------------------------------

  .page_index_grow {
    background-image: url("../img/homepage/Home3.jpg"); }

  //-----------------------------------------------

  .page_index_live {
    background-image: url("../img/homepage/Home4b.jpg");

    .btn {
      margin-top: $gutter_width*2; }

    .percent {
      position: absolute;
      right: 0;
      left: auto;
      bottom: grid_sz(0,5); } }

  //----------------------------------------------
  .page_index_accurate {
    background-image: url("../img/homepage/Home5.jpg");
    position: relative;


    .section_title,.section_para {
      color: $color_white; }

    .btn {
      margin-top: $gutter_width*2; }

    @include bg_block('before','right',$color_theme);


    .col-sm-6 {
      padding-left: 1.5*$gutter_width;
      padding-right: 1.5*$gutter_width;
      text-align: center;
      h3,p {
        color: $color_white; } }

    .btn {
      margin-bottom: $gutter_width*4; }
    .acc_sm_pic {
      // background-color: $color_theme
      border: none;
      width: 50%;
      height: 100%;
      cursor: pointer;
      &:hover {
        &:before {
          opacity: 1; } }
      &:before {
        @include pseudo_ab_full;
        @include ab_center;
        background-color: rgba($color_theme,0.5);
        opacity: 0;
        transition: 0.5s;
        z-index: 2; }
      &:after {
        @include pseudo_ab_full;
        @include ab_center;
        background-size: cover;
        z-index: 1; }
      h4 {
        color: $color_white;
        font-size: 20px;
        letter-spacing: 3px;
        @include ab_center;
        z-index: 3; } }

    .acc_sm_pic[data-type="1"]:after {
      background-image: url("../img/homepage/Home6-1.jpg"); }
    .acc_sm_pic[data-type="2"]:after {
      background-image: url("../img/homepage/Home6-4.jpg"); }
    .acc_sm_pic[data-type="3"]:after {
      background-image: url("../img/homepage/Home6-2.jpg"); }
    .acc_sm_pic[data-type="4"]:after {
      background-image: url("../img/homepage/Home6-3.jpg"); }


    .type_container {
      position: absolute;
      bottom: 0px;
      height: grid_sz(0,14);
      flex-direction: column;
      z-index: 100;

      .row {
        display: flex;
        height: 50%;
        width: grid_sz(8,8);
        &:nth-child(1) {
          left: grid_sz(4,4); }
        &:nth-child(2) {
          left: grid_sz(2,2); } } } }



  //------------------------------------------------------

  .page_index_sponsor {
    min-height: initial;
    background-color: $color_grey_bg;

    .container {
      height: auto;
      padding-top: grid_sz(0,3);
      padding-bottom: grid_sz(0,2);
      justify-content: space-around; }

    .sponsorlogo {
      width: grid_sz(2,2);
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      // padding-left: grid_sz(0,1)
      // padding-right: grid_sz(0,1)

      mix-blend-mode: darken; } }

  include footer {} }
