//bootstrap setting
.btn {
  border-radius: 2px;
  letter-spacing: 2px;
  // margin-top: $gutter_widt
  padding: 10px grid_sz(0,1);
  font-weight: 300;
  font-size: 15px;
  letter-spacing: 4px;
  text-decoration: none !important;
  cursor: pointer;
  @include trans; }

.btn-primary {
  border: none;
  background-color: $color_theme;
  color: $color_white;
  &:hover {
    background-color: lighten($color_theme,15);
    color: $color_white; } }

.btn-primary-lighter {
  border: none;
  background-color: lighten($color_theme,7);
  color: $color_white;
  &:hover {
    background-color:  lighten($color_theme,15);
    color: $color_white;
    border-color:  lighten($color_theme,7); } }

.btn-transparent {
  background-color: transparent;
  border: solid 1px $color_white;
  color: $color_white;
  &:hover {
    background-color: $color_white;
    color: $color_theme;
    border-color: $color_white; } }

@mixin cover_opacity($color: black) {
  display: block;
  content: "";
  position: absolute;
  @include size(100%,100%);
  left: 0;
  top: 0;
  background-color: $color; }

