nav {
  $nav_height: grid_sz(0,3);
  width: 100%;
  height: $nav_height;
  position: fixed;
  font-size: 17px;
  @include colors_bg_text(white,$color_grey_dark);
  padding-top: grid_sz(0,1)/2;
  border-top: solid 3px $color_theme;
  z-index: 200;
  @include trans;

  * {
    color: $color_grey;
    text-decoration: none;
    font-weight: 400; }

  .row {
    display: flex; }

  //---
  .navbar-header {
    width: grid_sz(3,2);
    img {
      height: 50px;
      @include trans; } }
  .navbar-left {
    margin-left: grid_sz(0,1);
    width: grid_sz(7,6); }

  .navbar-right {
    width: grid_sz(2,2);
    display: flex;
    align-items: space-between; }

  .navbar-left,.navbar-right {
    margin-top: grid_sz(0,1)/2;
    @include trans; }

  //---



  .navbar-nav li {
    list-style: none;
    display: inline-block;
    margin-right: grid_sz(0,1);

    & a.router-link-active {
      @include colors_bg_text(transparent,$color_theme);
      border-bottom: solid 1px $color_theme;
      &:hover {
        color: $color_theme;
        background-color: transparent; } }

    & > a {
      padding-bottom: 6px;
      border-bottom: solid 1px transparent;
      @include trans; }


    & ul.subnav {
      width: 100vw;
      position: fixed;
      @include pos(0,$nav_height);
      opacity: 0;
      overflow: hidden;
      max-height: 0px;
      height: 0px;
      transition: 0.5s 0.2s, top 0.5s 0s;
      text-align: center;
      background-color: rgba($color_theme,0.9);
      li a {
        color: $color_white; } }

    &:hover {
      a {
        border-bottom: solid 1px $color_theme; }
      .subnav {
        max-height: 200px;
        height: grid_sz(0,3);
        opacity: 1;
        z-index: 100;
        padding: grid_sz(0,1) 0px; } } }

  //---- when at top of the page
  &.at_top {
    $nav_height_at_top: grid_sz(0,5);
    height: $nav_height_at_top;
    padding: grid_sz(0,1);

    .navbar-header {
      width: grid_sz(3,2); }

    .navbar-left,.navbar-right {
      margin-top: grid_sz(0,1); }
    img {
      height: 65px; }

    .navbar-nav li {
      & ul.subnav {
        @include pos(0,$nav_height_at_top); } } }

  @include range_width(0px,1080px) {
    .func_lang,.func_search {
      display: none; } }


  @include range_width(0px,500px) {
    .navbar-right .function {
      display: none; }
    .navbar-right {
      display: inline-block;
      width: auto; } } }


