}

.slide_bullet li {
  display: block;
  width: 9px;
  height: 9px;
  border-color: #777;
  border-radius: 50%;
  margin-bottom: 16px;
  cursor: pointer;
  transition-duration: 0.5s;
  transition-delay: 0;
  position: relative;
}

.slide_bullet li:after {
  content: "";
  display: block;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 6px;
  height: 6px;
  background-color: #3e3e3e;
  transition-duration: 0.5s;
}

.slide_bullet li:hover {
