  background-color: #3FBFBB;
  width: 12px;
  height: 12px;
}

.slide_bullet li.active {
  background-color: #3FBFBB;
}

.slide_bullet li.active:after {
  background-color: #3FBFBB;
  width: 12px;
  height: 12px;
}

