.page_contact .section_hero {
  min-height: 70vh;
  padding-top: 108px;
  padding-bottom: 144px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url("../img/homepage/Contact1.jpg");
  background-size: cover;
  background-position: center center;
}

.page_contact .section_hero .container.flex {
  align-items: flex-start;
  padding-left: 120px;
}

.page_contact .section_hero:before {
  display: block;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(255, 255, 255, 0.1);
}

.page_contact .section_hero, .page_contact .section_hero .date, .page_contact .section_hero h1 {
  margin: 0;
}

.page_contact .section_hero .tag {
  padding: 12px;
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.8);
}

.page_contact .section_hero h1 {
  letter-spacing: 14px;
  margin-top: 48px;
  margin-bottom: 48px;
  font-weight: 300;
}

.page_contact .section_hero .description {
  max-width: 50%;
}

.page_contact .section_form ul.nav_line_split {
  padding: 0;
  margin: 0;
}

.page_contact .container.flex {
  align-items: flex-start;
}

.page_contact .form-group {
  position: relative;
  width: 100%;
}

.page_contact label {
  position: absolute;
  font-weight: 300;
  color: #777;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 50px;
  top: 20px;
  text-align: left;
  border-right: solid 2px rgba(119, 119, 119, 0.4);
  padding-right: 20px;
  z-index: 10;
}

.page_contact input, .page_contact select, .page_contact textarea {
  padding-left: 90px !important;
  border: none;
  width: 100%;
  border: solid 1px rgba(119, 119, 119, 0.4);
  border-radius: 0px;
  margin-bottom: 24px;
  min-height: 40px;
  letter-spacing: 2px;
  color: #777;
}

.page_contact #select_contact {
  min-height: 40px;
  height: 40px;
}

.page_contact textarea {
  padding: 10px 18px !important;
  letter-spacing: 2px;
  font-size: 15px !important;
}
