@import "common";
.page_contact {
  * {
 }    // border: solid 1px

  section {
    min-height: 0px;
    position: relative;
    .section_title {
      // padding-top: $gutter_width*4

      font-weight: 300;
      font-size: 44px;
      opacity: 0.8; } }

  .section_hero {
    min-height: 70vh;
    padding-top: $column_width*1.5;
    padding-bottom: $column_width*2;

    display: flex;
    align-items: center;
    justify-content: center;
    background: url("../img/homepage/Contact1.jpg");
    background-size: cover;
    background-position: center center;

    .container.flex {
      align-items: flex-start;
      padding-left: grid_sz(1,2); }

    &:before {
      @include cover_opacity(rgba(white,0.1)); }
    &,.date,h1 {
      // color: white
      margin: 0; }
    .tag {
      padding: $gutter_width*0.5;
      display: inline-block;
      background-color: rgba($color_white,0.8); }
    h1 {
      letter-spacing: 14px;
      margin-top: $gutter_width*2;
      margin-bottom: $gutter_width*2;
      font-weight: 300; }
    .description {
      max-width: 50%; } }

  .section_form {
    ul.nav_line_split {
      padding: 0;
      margin: 0; } }

  .container.flex {
    align-items: flex-start; }

  .form-group {
    position: relative;
    width: 100%; }

  label {
    position: absolute;
    font-weight: 300;
    color: $color_grey_light;
    @include ab_center;
    left: 50px;
    top: 20px;
    text-align: left;
    border-right: solid 2px rgba($color_grey_light,0.4);
    padding-right: 20px;
    z-index: 10; }

  input,select,textarea {
    padding-left: 90px !important;
    border: none;
    width: 100%;
    border: solid 1px rgba($color_grey_light,0.4);
    border-radius: 0px;
    margin-bottom: grid_sz(0,1);
    min-height: 40px;
    letter-spacing: 2px;
    color: $color_grey_light; }

  #select_contact {
    min-height: 40px;
    height: 40px; }
  textarea {
    padding: 10px 18px !important;
    letter-spacing: 2px;
    font-size: 15px !important; } }
